import React, { Component } from 'react'
import {Button,Modal,message} from 'antd'
import {withRouter} from 'react-router-dom'
import {postAction } from '@/axios';
class MiddleTop extends Component {
    handleReset=()=>{
        Modal.confirm({
            title:'',
            content:'',
            okText:'',
            cancelText:'',
            onOk:()=>{
                this.props.handleReset()
            }
        })
    }
    handleSave=()=>{
        if(this.props.list.length>0){
            let tag=this.handleCheckValue()
            // table
            if(tag){
                message.warning('')
            }else{
                let list=this.props.list
                let params={
                    "formId": this.props.param.id,
                    "columnProperties": JSON.stringify(list),
                }
                postAction('/ybt/form/save',params).then(res=>{
                    if(res.success){
                        message.success('') 
                        setTimeout(()=>{
                            window.close()
                        },1500)                        
                    }else{
                        message.error(res.obj)   
                    }
                })
            }            
        }else{
            message.warning('')
            return
        }
    }

    /**
     *
     *
     * @memberof MiddleTop
     */   
    handlePreview=()=>{
        if(this.props.list.length>0){
            let tag=this.handleCheckValue()
            // table
            if(tag){
                message.warning('')
            }else{
                // table
                let list=this.props.list
                let params={
                    "formId": this.props.param.id,
                    "columnProperties": JSON.stringify(list),
                }
                postAction('/ybt/form/save',params).then(res=>{
                    if(res.success){
                        window.open(`${window.location.origin}/#/formManagement/previewForm?id=${this.props.param.id}&type=preview`)
                    }else{
                        message.error(res.obj)   
                    }
                })
            }
            
        }else{
            message.warning('')
            return
        }
        // 
    }
    /**
     *     *
     * @memberof MiddleTop
     */
    handleCheckValue=()=>{
        let tag=false
        this.props.list.forEach(item=>{
            if(item.children && item.children.length>0){
                item.children.forEach(i=>{
                    if(i.common.onlyId === '' || i.common.name === ''){
                        tag=true
                    }
                })
            }
            if(item.common.onlyId === '' || item.common.name === ''){
                tag=true
            }
        })
        return tag
    }
    render() {
        return (
            <div className='top'>
                <span style={{float:'left',paddingLeft:'20px',fontWeight:'bold'}}>{window.sessionStorage.getItem('formManageName')}</span>
                <Button onClick={this.handleSave} type="primary"  icon="save" ></Button>
                <Button style={{marginLeft:10}} onClick={this.handlePreview} type="primary"  icon="eye" ></Button>
                <Button style={{marginLeft:10}} onClick={this.handleReset} type="primary"  icon="reload" ></Button>
            </div>
        )
    }
}

export default withRouter(MiddleTop)
