import React, { Component } from 'react'
import {Form,Input,Switch,Select,InputNumber } from 'antd'
import 'rc-color-picker/assets/index.css';
import ColorPicker from 'rc-color-picker';
const Option=Select.Option
const FormItem=Form.Item
class PropComponent extends Component {
    constructor(props){
        super(props)
        this.state={
            backgroundColor:props.activeItem.style.backgroundColor,
            color:props.activeItem.style.color,
            fontName:[
                {
                    name:'',
                    value:''
                },
                {
                    name:'',
                    value:''
                }
            ],
            fontLine:[
                {
                    name:'',
                    value:'none'
                },
                {
                    name:'',
                    value:'overline'
                },
                {
                    name:'',
                    value:'underline'
                },
                {
                    name:'',
                    value:'line-through'
                }
            ]
        }
    }
    componentDidUpdate(preProps){
        if(preProps.activeItem.id !== this.props.activeItem.id){
            // 
            // 
            this.setState({
                backgroundColor:this.props.activeItem.style.backgroundColor,
                color:this.props.activeItem.style.color
            },()=>{
                this.props.form.resetFields()
            })
        }
    }
    changeBackColor=colors=>{
        this.setState({
            backgroundColor:colors.color
        })
    }
    changeFontColor=colors=>{
        this.setState({
            color:colors.color
        })
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        let {activeItem}=this.props,style=activeItem.style
        const formItemLayout = {
            labelCol: {
              span:8
            },
            wrapperCol: {
              span:16
            },
        };
        let showbgColor = true;//
        let showfontColor = true;//
        let showfontDecoration = true;//
        let activeType = this.props.activeItem.type;
        if(activeType==='dropDown'|| activeType==='date'){
            showbgColor = false;
        }
        if(activeType==='checkbox'||activeType==='radio'|| activeType==='date'){
            showfontColor = false;
        }
        if(activeType==='dropDown'|| activeType==='checkbox'||activeType==='radio'|| activeType==='date'){
            showfontDecoration = false;
        }
        return (
            <Form {...formItemLayout} style={{padding:'0 10px'}}>
                <FormItem label=''>
                    {
                        getFieldDecorator('fontSize',{
                            initialValue:style.fontSize,
                        })(<InputNumber/>)
                    }
                </FormItem>   
                <FormItem label=''>
                    {
                        getFieldDecorator('fontFamily',{
                            initialValue:style.fontFamily,
                        })(<Select>
                            {this.state.fontName.map((item,index)=><Option key={index} value={item.value}>{item.name}</Option>)}
                        </Select>)
                    }
                </FormItem> 
                {
                    showbgColor && 
                    <FormItem label=''>
                        {
                            getFieldDecorator('backgroundColor',{
                                initialValue:{
                                    alpha: 100,
                                    color: style.backgroundColor,
                                    open: false
                                }                            
                            })(<ColorPicker
                                animation="slide-up"
                                color={this.state.backgroundColor}
                                enableAlpha={false}
                                onChange={this.changeBackColor}
                                mode="RGB"
                                placement="bottomRight"
                            ><Input/></ColorPicker>)
                        }
                    </FormItem> 
                }
                {
                    showfontColor &&
                    <FormItem label=''>
                        {
                            getFieldDecorator('color',{
                                initialValue:{
                                    alpha: 100,
                                    color: style.color,
                                    open: false
                                }
                            })(<ColorPicker
                                animation="slide-up"
                                color={this.state.color}
                                enableAlpha={false}
                                onChange={this.changeFontColor}
                                mode="RGB"
                                placement="bottomRight"
                            ><Input/></ColorPicker>)
                        }
                    </FormItem> 
                }
                <FormItem label=''>
                    {
                        getFieldDecorator('FontBold',{
                            initialValue:style.FontBold,
                            valuePropName:'checked'
                        })(<Switch/>)
                    }
                </FormItem>  
                <FormItem label=''>
                    {
                        getFieldDecorator('FontItalic',{
                            initialValue:style.FontItalic,
                            valuePropName:'checked'
                        })(<Switch/>)
                    }
                </FormItem>
                {
                    showfontDecoration &&
                    <FormItem label=''>
                        {
                            getFieldDecorator('textDecoration',{
                                initialValue:style.textDecoration,
                            })(<Select>
                                {this.state.fontLine.map((item,index)=><Option key={index} value={item.value}>{item.name}</Option>)}
                            </Select>)
                        }
                    </FormItem> 
                }
            </Form>
        )
    }
}
PropComponent=Form.create({
    onValuesChange:(props, changedFields, allFields)=>{
        // id
        props.handleUpdateProp('style',props.activeItem.id,allFields)
    }
})(PropComponent)
export default PropComponent

